import React from "react";
import { Link } from "react-router-dom";

export function Home() {
   return (
    <div>
      <ul className="home-menu">
        <li>
          <Link to="/signin"></Link>
        </li>
        <li>
          <Link to="/signup"></Link>
        </li>
      </ul>
      <p></p>
    </div>
  );
}
export default Home;

